import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import { Paciente } from '../models/paciente';
import { Mensagem } from '../models/mensagem';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ChatService {

  private chatDB: firebase.firestore.CollectionReference;

  constructor() { 
    this.chatDB = firebase.firestore().collection('chat');
  }

  /**
   * Busca todas as mensagens que fazem parte do grupo de um coordenador
   * @param coordenadorUID 
   */
  buscarMensagens(coordenadorUID: string) {
    return this.chatDB.where('coordenadorUID', '==', coordenadorUID).orderBy('data', 'desc').limit(50);
  }

  /**
   * Envia uma nova mensagem
   * @param mensagem 
   * @param remetente 
   */
  enviar(mensagem: string, remetente: Paciente) {
    let doc = this.chatDB.doc();
    let data = moment().format("YYYY-MM-DD HH:mm:ss") 
    console.log(remetente);
    let msg = new Mensagem(remetente.codigo, mensagem, data, doc.id, remetente.uid, remetente.coordenadorUID);
    console.log(msg);
    doc.set(Object.assign({}, msg));
  }
}
