import { Component, OnInit, ViewChild } from '@angular/core';
import { Mensagem } from 'src/app/models/mensagem';
import * as moment from 'moment';
import { PacienteService } from 'src/app/services/paciente.service';
import { ChatService } from 'src/app/services/chat.service';
import { Paciente } from 'src/app/models/paciente';
import { IonContent } from '@ionic/angular';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.page.html',
  styleUrls: ['./chat.page.scss'],
})
/**
 * Tela de Chat para os pacientes conversarem entre si
 * @author Carlos W. Gama
 */
export class ChatPage implements OnInit {

  paciente = new Paciente;

  @ViewChild('content', {static:false}) content:IonContent;

  //Mensagem enviada 
  mensagem:string = '';

  //Mensagens do chat
  mensagens:Mensagem[] = [];

  constructor(private pacienteService:PacienteService, private chatService: ChatService) { }

  async ngOnInit() {
    
    this.paciente = await this.pacienteService.getPacienteLogado();
    //Busca as mensagens como Observable
    this.chatService.buscarMensagens(this.paciente.coordenadorUID).onSnapshot(async (snapshot) => {
      this.mensagens = []
      snapshot.forEach(doc => {
        this.mensagens.unshift(Object.assign(new Mensagem, doc.data()))
      })
      await new Promise(resolve => { setTimeout(resolve, 500)});
      this.content.scrollToBottom(300);
      

    })
    //this.mensagem = await;
  }

  /** Envia a mensagem para o Chat */
  enviar() {
    this.chatService.enviar(this.mensagem, this.paciente);
    this.mensagem = '';
  }

}
